import TransitionManager from "jx/core/transitions/TransitionManager";
import ParticlesManager from "jx/core/particles/ParticlesManager";
import Filters from "jx/core/filters/Filters";
/**
 * Manage effects on components, particles, transitions, filters, ...
 */
export default class FXManager {
    constructor({ jx }) {
        this.jx = jx;
        this.transitions = new TransitionManager(this.jx);
        this.particles = new ParticlesManager(this.jx);
        this.filters = Filters;
    }
    createTransition(params) {
        return this.transitions.create(params);
    }
    createParticle(params) {
        return this.particles.create(params);
    }
}
